import React, { Component } from "react";
import { Card, Form, Button } from "react-bootstrap";
import { ThemeConsumer } from "styled-components";
import { postJobData } from "./postJobTempData";

export default class SelectPackage extends Component {
  state = {
    test: "1",
  };
  render() {
    return (
      <div>
        <Card>
          <Card.Header
            className="text-center"
            style={{
              color: "#fff",
              backgroundColor: "#007fc3",
              fontWeight: "bold",
            }}
            as="h3"
          >
            
          </Card.Header>
          <Card.Body style={{ fontSize: 18, fontWeight: 300 }}>
            <div style={{ marginBottom: 20 }}>
              <Form.Check
                //   disabled
                type={"radio"}
                label={`  -  10   30 `}
                //   id={`disabled-default-radio`}
              />
              <span style={{ color: "red", fontSize: 10, paddingLeft: 100 }}>
                ***  5  
                 15/07/2020 .
              </span>
            </div>
            <div style={{ marginBottom: 20 }}>
              <Form.Check
                type={"radio"}
                label={`  -  30    90 `}
              />
            </div>
            <div style={{ marginBottom: 20 }}>
              <Form.Check
                type={"radio"}
                label={`  -    1 `}
              />
            </div>
          </Card.Body>
        </Card>
      </div>
    );
  }

  nextStep = () => {
    console.log("SelectPackage nextStep");
    postJobData.previewData = {
      logoFileInput: null,
      bannerFileInput: null,
      imagepostjob1: null,
      imagepostjob2: null,
      imagepostjob3: null,
      imagepostjob4: null,
    };
    return true;
  };

  backStep = () => {
    console.log("SelectPackage backStep");
    return false;
  };
  // nextStep = (btnEvent) => {
  //   console.log("SelectPackage " + btnEvent);
  //   return true;
  // };
}
