import React, { Component } from "react";
import {
  Card,
  Form,
  Button,
  Row,
  Col,
  Container,
  Tabs,
  Tab,
} from "react-bootstrap";
import { postJobData } from "./postJobTempData";
import { FaFacebookSquare } from "react-icons/fa";
import { jobs } from "../../data";
import styled from "styled-components";

const Styles = styled.div`
  .card-header-tabs {
    border-bottom: 1px solid #dee2e6;
  }
`;
export default class PreviewPost extends Component {
  render() {
    return (
      <Styles>
        <div>
          {/* PreviewPost {JSON.stringify(postJobData)} */}
          <Card>
            <Card.Header
              className="text-center"
              style={{
                color: "#fff",
                backgroundColor: "#007fc3",
                fontWeight: "bold",
              }}
              as="h3"
            >
                
            </Card.Header>
            <Card.Body style={{ fontSize: 18, fontWeight: 300 }}>
              <div style={{ fontSize: 14 }}>
                <Container>
                  <Row>
                    <Col lg={2} style={{ marginBottom: 20 }}>
                      <img
                        src={postJobData.previewData.logoFileInput}
                        style={{ width: 128, height: 128 }}
                      />
                    </Col>
                    <Col lg>
                      <img
                        src={postJobData.previewData.bannerFileInput}
                        style={{ width: "100%", height: 128 }}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col
                      style={{
                        color: "#ff0000",
                        fontSize: 18,
                        fontWeight: "bold",
                        fontStyle: "italic",
                        textDecorationLine: "underline",
                      }}
                    >
                        
                    </Col>
                    <Col style={{ textAlign: "right" }}>
                       22/06/2020
                    </Col>
                  </Row>
                  <Card.Text
                    style={{
                      color: "#007bff",
                      fontSize: 16,
                      fontWeight: "bold",
                      fontStyle: "italic",
                      textDecorationLine: "underline",
                    }}
                  >
                    
                  </Card.Text>
                  <Card.Text>
                     - 555     
                         10120
                  </Card.Text>
                  <Card.Text>
                     - 
                  </Card.Text>
                  <Card.Text> - </Card.Text>
                  <Card.Text> - 350  </Card.Text>
                  {/* <hr></hr> */}

                  <Tabs
                    defaultActiveKey="jobInfo"
                    transition={false}
                    className="card-header-tabs"
                  >
                    <Tab eventKey="jobInfo" title="">
                      <JobInfo />
                    </Tab>
                    <Tab eventKey="companyInfo" title="">
                      <CompanyInfo />
                    </Tab>
                  </Tabs>
                </Container>
              </div>
            </Card.Body>
          </Card>
        </div>
      </Styles>
    );
  }

  finishStep = () => {
    console.log("PreviewPost nextStep");
    var jobsTemp = jobs[0];
    console.log("PreviewPost Add job");
    jobs.push(jobsTemp);
    console.log("PreviewPost Add success");
    return true;
  };

  backStep = () => {
    console.log("PreviewPost backStep");
    return true;
  };
}

